import React, { memo, useMemo } from 'react';
import { COLORS, IRestProps, mergeRestProps } from '../../common';
import { StyledSliderLine, LineContent } from './styled';

type LineProps = {
  // 以下为内部属性，PropsFromSlider
  start?: number,
  sliderLength?:number,
  end?: number,
  disable?:boolean,

  // 以下为用户自定义属性
  lineBgColor?: string,
  lineBgColorDisabled?: string,
  lineColor?: string,
  lineColorDisabled?: string,
} & IRestProps;

export const SliderLine = memo((props: LineProps) => {
  const { start = 0, end = 0, sliderLength = 0, disable,
    lineBgColor = COLORS.COMMON.LINE_HARD_COLOR,
    lineBgColorDisabled = COLORS.COMMON.LINE_HARD_COLOR,
    lineColor = COLORS.DING_BLUR,
    lineColorDisabled = COLORS.LIGHT.LEVEL3_BASE_COLOR,
    style, ...rest } = props;
  const left = start > end ? end : start;
  const length = Math.abs(start - end);
  const bgLineStyle = useMemo(() => {
    return {
      background: disable ? lineBgColorDisabled : lineBgColor,
      ...style,
    }
  }, [disable, lineBgColorDisabled, lineBgColor, style]);

  return (
    <StyledSliderLine {...mergeRestProps({
      ...rest,
      style:bgLineStyle
    }, '')}>
      <LineContent
        role="content"
        style={{
          background: disable ? lineColorDisabled : lineColor,
          left: `${left / sliderLength * 100}%`,
          width: `${length / sliderLength * 100}%`,
        }}
      />
    </StyledSliderLine>
  );
});

SliderLine.displayName = 'sliderLine';